	module.exports = {"data":{"markdownRemark":{"html":"<p>Intel produced this fantastic map yesterday at the <a href=\"http://www.podtech.net/home/5167/intel-research-day-showcase-for-creativity\">2008 Research@Intel Day</a>. Red countries have higher rates of technology adoption. This is really valuable data for thinking about how to influence the adoption of technology, and for thinking about the <a href=\"http://en.wikipedia.org/wiki/Information_and_Communication_Technologies_for_Development\">ICT4D</a> political spectrum in more than two shades of grey (or red and orange I guess). I think anyone who reads about this kinda stuff already has some vague map like this in their head. The only real surprise is how slow, relatively, the US is (I guess South Africa being flat is a surprise too). But the country-by-country resolution here adds such a valuable data point to the conversation about the <a href=\"http://en.wikipedia.org/wiki/Bottom_of_the_Pyramid\">BoP</a> and the overall role of technology in the developing world.  (It's also a pretty damn good crystal ball on the future distribution of economic development). I want one for my wall.</p>\n<img src=\"/static/images/intelsm.jpg\" alt=\" \" />","frontmatter":{"created_at":"June 13, 2008","path":"/2008/06/13/the-unevenly-distributed-future-visualized","title":"The Unevenly Distributed Future (of Mobile Application Design), Visualized","tags":["futurism","BoP","intel","ict4d"]}}},"pathContext":{}}

